// 結帳資訊
.cart-detail {
	$root: &;

	@include breakpoint(large) {
		padding-left: 1.5rem;
	}

	@include breakpoint(xlarge) {
		padding-left: 2.5rem;
	}

	&__cont {
		@include pd-sec;

		@include breakpoint(medium down) {
			padding: 0;
			box-shadow: none;
			background-color: transparent;
		}

		@include breakpoint(large) {
			padding: rem-calc(20 24);
		}
	}

	&__title {
		margin-bottom: rem-calc(24);
		font-weight: $font-bold-b;
	}

	// List
	&__list {
		margin: 0 0 rem-calc(56);
		padding: 0;
		list-style: none;

		@include breakpoint(medium) {
			margin-bottom: rem-calc(36);
		}

		&__item {
			$el: &;

			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-bottom: rem-calc(12);

			#{$el}__txt {
				font-weight: $font-bold-m;
			}

			@include breakpoint(medium) {
				margin-bottom: rem-calc(20);
			}

			&:last-child {
				margin-top: rem-calc(20);
				margin-bottom: 0;
				padding-top: rem-calc(20);
				border-top: rem-calc(1) solid $bfLightGray;
				align-items: baseline;

				#{$el}__title {
					font-weight: $font-bold-b;

					@include breakpoint(medium) {
						font-size: rem-calc(18);
						font-weight: $font-bold-m;
					}
				}

				#{$el}__txt {
					font-size: rem-calc(24);
					font-weight: $font-bold-b;
					text-align: right;
					color: $primary-orange;

					@include breakpoint(xlarge) {
						font-size: rem-calc(28);
					}

					@include breakpoint(xxlarge) {
						font-size: rem-calc(34);
					}
				}

				@include breakpoint(medium) {
					margin-top: 0;
					padding-top: rem-calc(24);
				}
			}
		}
	}

	// 剩餘多少Coin
	.coin-balance {
		display: block;
		margin-top: rem-calc(4);
		font-size: rem-calc(12);
		font-weight: normal;
		white-space: nowrap;

		@include breakpoint(medium) {
			font-size: rem-calc(14);
		}
	}

	// 隱藏小計
	&.is-hide-subtotal {
		#{$root} {
			&__title {
				display: none;
			}

			&__list__item {
				&:not(.cart-detail--total) {
					display: none;
				}

				&.cart-detail--total {
					margin-top: 0;
					padding-top: 0;
					border-top: 0;
				}
			}
		}
	}
}

// --------------------------------------------------------

// 商品卡片
.pd-card {
	$root: &;

	&-cart {
		#{$root}__title {
			max-width: 18.5em;
			font-weight: $font-bold-b;

			@include breakpoint(small down) {
				line-height: 1.6;
			}
		}

		#{$root}__century {
			font-weight: $font-bold-m;
		}

		#{$root}__price {
			color: inherit;
		}

		#{$root}__price-box.hori {
			#{$root}__price {
				font-size: inherit;
			}

			@include breakpoint(small only) {
				font-size: rem-calc(14);
			}
		}
	}
}
