	&__button {
		margin-bottom: 0;
		width: 100%;
	}
}

// 商品卡片 (部分移動到 component)
.pd-card {
	$root: &;

	margin-bottom: rem-calc(24);

	@include breakpoint(medium) {
		margin-bottom: rem-calc(60);
	}
}

