// 商品資訊
.checkbox-group {
	margin-bottom: rem-calc(43);

	@include breakpoint(medium) {
		margin-bottom: rem-calc(24);
	}

	.checkbox-box:not(:last-child) {
		margin-bottom: rem-calc(13);
	}

	.checkbox + label {
		margin-right: 0;
		line-height: normal;

		&::before {
			top: 0.65em;
		}
	}
}

// app fixed 資訊
.pd-fixed-info {
	display: none;
	padding: rem-calc(10 20);

	.btn {
		&:not(:last-child) {
			margin-bottom: rem-calc(10);
		}
	}
}

// --------------------------------------------------------

// 交易列表
.cart-cont-container {
	@include breakpoint(small down) {
		padding-bottom: rem-calc(50);
	}
}

.pd-card-cart {
	.order-detail__info {
		@include breakpoint(small only) {
			&__title {
				white-space: nowrap;
			}

			&__item {
				justify-content: space-between;

				// 選擇數量
				&.count {
					align-items: flex-start;
					margin-top: rem-calc(12);

					.order-detail__info__title {
						margin-top: rem-calc(11);
					}
				}
			}
		}

		@include breakpoint(medium) {
			&.has-count-input {
				display: flex;
				align-items: flex-start;
				justify-content: space-between;

				.order-detail__info__item {
					margin-top: rem-calc(5 + 11);

					&.count {
						margin-top: rem-calc(5);

						.order-detail__info__title {
							display: none;
						}
					}
				}
			}
		}
	}

	.count-box {
		width: rem-calc(120);

		&__btn {
			font-size: rem-calc(10);
		}

		&__input {
			padding-top: rem-calc(8);
			padding-bottom: rem-calc(8);
		}
	}
}

// 優惠券選擇結果
.coupon-result {
	display: flex;
	align-items: center;
	margin-right: rem-calc(10);

	&__text {
		$el: &;

		font-size: rem-calc(15);
		line-height: 1.4;

		@at-root .chrome.mac {
			#{$el} {
				transform: translateY(0.5px);
			}
		}
	}

	&__btn {
		$el: &;

		color: #999;
		margin-left: rem-calc(8);

		// SIT F168: iOS. OS 系統不明原因會產生偏移
		@at-root %move-before {
			#{$el} {
				transform: translateY(-1px);

				.be-icon {
					&::before {
						transform: translateY(1px);
					}
				}
			}
		}

		@at-root .ios {
			@extend %move-before;
		}

		@at-root .safari.mac {
			@extend %move-before;
		}

		@include breakpoint-hover {
			&:hover,
			&:focus {
				color: $primary-color;
			}
		}
	}

	@include breakpoint(large) {
		&.auto {
			max-width: calc(100% - #{rem-calc(75 + 10)});
			flex: initial;
		}
	}

	@include breakpoint(xlarge) {
		&.auto {
			max-width: calc(21em + #{rem-calc(23 + 8)});
		}
	}
}

// --------------------------------------------------------

// 交易成功
.cart-cont-container.statue-success {
	.pd-card {
		&__link {
			padding: 0;
		}

		&__title {
			margin-bottom: rem-calc(8);
			max-width: none;
			width: 100%;
		}

		// Price
		&__price-box {
			position: relative;
			align-items: baseline;

			@include breakpoint(medium) {
				justify-content: flex-end;
			}
		}

		&__century {
			position: relative;
			font-weight: $font-bold-m;

			@include breakpoint(small down) {
				visibility: hidden;

				&::after {
					content: attr(data-after);
					visibility: visible;
				}
			}
		}
	}

	@include breakpoint(medium down) {
		.pd-card {
			margin-bottom: rem-calc(40);
		}

		.cart-statue {
			&__cover-title {
				margin-bottom: rem-calc(40);
			}

			&__title {
				margin-bottom: rem-calc(20);
				text-align: center;
			}
		}
	}

	@include breakpoint(medium only) {
		.cart-statue {
			&__cover {
				padding-right: rem-calc(20);
			}
		}
	}

	@include breakpoint(medium) {
		.button-box {
			.btn {
				min-width: rem-calc(216);
			}
		}
	}

	@include breakpoint(large) {
		padding-bottom: rem-calc(110);

		.pd-card {
			margin-bottom: rem-calc(80);
		}
	}
}

.cart-statue {
	margin-top: rem-calc(20);
	margin-bottom: rem-calc(40);

	@include breakpoint(large) {
		margin-top: rem-calc(60);
		margin-bottom: rem-calc(65);
	}

	&__cover {
		@include breakpoint(large) {
			width: rem-calc(265);
		}

		&-img {
			width: rem-calc(155);
			margin-bottom: rem-calc(24);

			@include breakpoint(medium) {
				width: rem-calc(178);
				margin-bottom: rem-calc(36);
			}
		}

		&-title {
			color: $primary-color;
			font-weight: $font-bold-b;

			@include breakpoint(small down) {
				font-size: rem-calc(20);
			}
		}

		&-txt {
			@include breakpoint(small down) {
				font-weight: normal;
			}
		}
	}

	&__title {
		margin-bottom: rem-calc(28);

		@include breakpoint(medium) {
			padding-top: rem-calc(7);
		}
	}

	&__txt {
		margin-bottom: 0;
		font-weight: normal;

		&:not(:last-child) {
			margin-bottom: rem-calc(30);

			@include breakpoint(medium down) {
				margin-bottom: rem-calc(25);
			}
		}

		.bold {
			font-weight: $font-bold-b;
		}

		.link {
			font-weight: $font-bold-m;
		}
	}
}

.cart-order {
	&__id {
		margin-bottom: rem-calc(16);
		line-height: 1.29;
		font-size: rem-calc(12);
		color: $color-tip-dark;

		@include breakpoint(medium) {
			margin-bottom: rem-calc(20);
			font-size: rem-calc(14);
		}
	}

	&__item {
		padding: rem-calc(20);

		@include breakpoint(medium) {
			padding: rem-calc(30);
		}

		&:not(:last-child) {
			border-bottom: $border-global;
		}
	}

	&__qrcode {
		&-title {
			@include breakpoint(small only) {
				text-align: center;
			}

			@include breakpoint(medium) {
				font-size: rem-calc(20);
			}
		}

		&-img-box {
			@include breakpoint(small only) {
				display: none;
			}

			@include breakpoint(medium) {
				margin-left: percentage(64 / 680);
			}
		}

		&-img {
			width: rem-calc(96);
		}

		&-txt {
			margin-bottom: 0;
			text-align: justify;
		}
	}

	// 訂單金額
	.pd-card__century {
		@include breakpoint(medium) {
			font-size: rem-calc(15);
		}
	}

	// 訂單價格
	.pd-card__price {
		@include breakpoint(medium) {
			font-size: rem-calc(24);
		}
	}
}

// --------------------------------------------------------

// 交易失敗
.cart-cont-container.statue-fail {
	padding-bottom: rem-calc(60);

	@include breakpoint(large) {
		padding-bottom: rem-calc(137);
	}

	.button-box {
		.btn {
			width: rem-calc(150);

			@include breakpoint(medium) {
				width: rem-calc(216);
			}
		}
	}

	.cart-statue {
		margin-bottom: rem-calc(40);

		@include breakpoint(large) {
			margin-bottom: rem-calc(60);
		}

		&__cover {
			width: auto;

			&-img {
				width: rem-calc(158);
				@include breakpoint(medium) {
					width: rem-calc(181);
				}
			}

			&-title {
				margin-bottom: rem-calc(16);
				@include breakpoint(medium) {
					margin-bottom: rem-calc(20);
				}
			}

			&-txt {
				font-weight: normal;
			}
		}
	}
}

// --------------------------------------------------------

// 我的票券
.coupon-box {
	$root: &;

	@include breakpoint(medium) {
		max-width: rem-calc(300);
	}

	.pd-spec__list {
		max-height: 55vh;
		overflow-y: auto;
	}

	&__list {
		margin: 0;
		margin-bottom: rem-calc(20);
		list-style: none;
	}

	&__sub-title {
		font-size: rem-calc(12);
		color: #666;
	}

	&__item {
		position: relative;

		&:not(:last-child) {
			border-bottom: $border-global;
		}
	}

	&__label {
		padding: rem-calc(20);
		padding-bottom: rem-calc(16);
		width: 100%;
		color: $primary-dark;

		@include bem-tag(label) {
			display: block;
			margin: 0;
		}
	}

	&__top {
		width: 100%;
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
	}

	&__item-title {
		font-size: rem-calc(15);
		font-weight: $font-bold-m;
		line-height: 1.47;
	}

	&__status {
		display: block;
		margin-left: rem-calc(15);
		padding-right: rem-calc(8);
		padding-left: rem-calc(8);
		font-size: rem-calc(15);
		color: $white;
		background: $primary-color;
		border-color: $primary-color;
		cursor: pointer;

		&::before {
			content: '使用';
		}
	}

	&__time {
		font-size: 12px;
		line-height: 1.33;
		color: #666;
	}

	&__radio {
		position: absolute;
		opacity: 0;
		cursor: pointer;
		margin-bottom: 0;

		&:checked {
			~ #{$root}__label {
				position: relative;
				// overflow: hidden;

				// SIT F167 - 客戶不要該效果
				// &::before {
				// 	content: '';
				// 	position: absolute;
				// 	left: 0;
				// 	top: 0;
				// 	width: 100%;
				// 	height: 100%;
				// 	background: linear-gradient(
				// 		to right,
				// 		rgba($primary-color, 0) 0%,
				// 		rgba($primary-color, 0.1) 50%,
				// 		rgba($primary-color, 0) 100%
				// 	);
				// 	animation: coupon-selected 1s forwards;

				// 	@keyframes coupon-selected {
				// 		0% {
				// 			transform: translateX(-100%);
				// 		}

				// 		100% {
				// 			transform: translateX(100%);
				// 		}
				// 	}
				// }

				#{$root} {
					&__status {
						color: $primary-color;
						background: transparent;

						&::before {
							content: '選定';
						}
					}
				}
			}
		}

		&:disabled {
			~ #{$root}__label {
				color: #999;
				cursor: not-allowed;

				#{$root} {
					&__status {
						background-color: #ddd;
						border-color: #ddd;
						cursor: not-allowed;
					}
				}
			}
		}
	}
}
