// 購買紀錄詳情頁
.order-detail {
	// 價格 + 數量
	&__info {
		margin-top: rem-calc(10);
		margin-bottom: 0;

		&__item {
			display: flex;
			align-items: center;
			margin-top: rem-calc(5);
		}

		&__title {
			margin-right: rem-calc(10);
			width: 2em;
			font-size: percentage(14 / 16);
			font-weight: $font-bold-m;
		}
	}
}
